<template>
  <div class="zero">
    <!-- 顶部图片 -->
    <img class="zero__img-auto" :src="`${baseUrl}zeroLive/header.png`" />
    <!-- 手机号 -->
    <div class="zero__form-item mt__70">
      <p class="ml__30 mr__20">+86</p>
      <img src="@/assets/zeroLive/icon_phone-arrow.png" alt="" />
      <input
        id="phoneInput"
        ref="phoneInput"
        placeholder="请输入手机号"
        v-model.trim="phone" />
    </div>
    <!-- 验证码 -->
    <div class="zero__form-item mt__20">
      <input placeholder="请输入验证码" v-model.trim="code" />
      <CountDownBtn
        class="pl__10 phone-code"
        :phone="phone"
        :params="{ product_sn: 'PRODUCT_SN_XIAOBAI_0' }"
        :get-code-success-back="getCodeSuccessBack"
        :get-code-error-back="getCodeErrorBack"></CountDownBtn>
    </div>
    <p
      class="zero__state mt__20 mb__20"
      v-show="stateIsShow"
      @click="stateModelShow = true">
      《个人信息授权与保护声明》
    </p>
    <!-- 理解报名按钮 -->
    <div class="zero__form-btn-wrap">
      <div class="mt__20" @click="onZeroSubmitPay">0元抢购</div>
    </div>
    <div class="zero__line"></div>
    <div class="zero__line-title">最新报名客户</div>
    <VanSwipe
      class="zero__swipe-box"
      :autoplay="3000"
      :show-indicators="false"
      vertical>
      <VanSwipeItem v-for="(item, index) in swipeList" :key="index">
        <div
          v-for="(el, index) in item"
          :key="index"
          class="zero__swipe-box__item">
          <p>{{ el.phone }}</p>
          <p>{{ el.state }}</p>
          <p>{{ el.time }}</p>
        </div>
      </VanSwipeItem>
    </VanSwipe>
    <!-- 底部图片 -->
    <img
      :class="['zero__img-auto', { mt__40: !index }]"
      v-for="(item, index) in 4"
      :key="index"
      :src="require(`@/assets/zeroLive/${index + 1}.png`)" />
    <img
      class="zero__img-auto"
      src="@/assets/zeroLive/5c.png"
      alt="">
    <!-- 备案图片 -->
    <div class="right">
      <div class="name">北京微淼财商科技有限公司</div>
      <div class="num">京ICP备19026951号-4</div>
    </div>
    <!-- 底部按钮 -->
    <div class="zero__footer">
      <div class="zero__footer__topic">
        <div>
          <!-- <i class="zero__footer__topic__title">市场价199元</i> -->
          <div class="zero__footer__topic__number">限时特惠</div>
        </div>
        <p class="zero__footer__topic__money"><i>0</i>元</p>
      </div>
      <div class="zero__footer__btn" @click="onZeroSubmitPay">
        <span>免费报名</span>
      </div>
    </div>
    <Statement v-if="stateModelShow" @onCloseMask="stateModelShow = false" />
  </div>
</template>

<script>
import { Swipe, SwipeItem, Dialog } from 'vant'
import swipeList, { originalConfig } from './config'
import { isPhone, getSearch } from '@/utils'
import CountDownBtn from '@/components/CountDownBtn'
import Statement from '@/components/Statement'

export default {
  components: {
    VanSwipe: Swipe,
    VanSwipeItem: SwipeItem,
    CountDownBtn,
    Statement
  },
  data () {
    return {
      stateIsShow: getSearch('medId') === '1610', // 1610 快手渠道
      stateModelShow: false, // 声明弹窗 快手渠道需要显示此弹窗
      ...originalConfig, // 埋点数据
      swipeList, // 轮播数组
      phone: '', // 手机号
      code: '', // 验证码
      paytype: 1 // 支付类型
    }
  },
  created () {
    this.init()
  },
  methods: {
    /**
     * @Date: 2021-01-08 10:54:08
     * @author: zw
     * @description: 如果缓存的手机号可以与库中匹配,跳转到引导页
     */
    async init () {
      const phone = localStorage.getItem('buyPhone')
      if (!phone) return
      try {
        await this.$apis.payment.checkOrder({
          phone,
          product_sn: 'PRODUCT_SN_XIAOBAI_0'
        })
        //
      } catch (err) {
        const { code, data } = err
        if (code === 30006) {
          this.hrefToSuccess(data.product_sn, data.order_num)
        }
      }
    },
    /**
     * @Date: 2021-01-04 10:27:08
     * @author: zw
     * @description: 0元直播,没有支付弹窗,直接支付
     */
    async onZeroSubmitPay (model = {}) {
      if (this.click) return
      this.click = true
      const { successBack = this.paySuccessBack, errorBack = this.payErrorBack } = model
      try {
        const { phone, code } = this
        if (!isPhone(phone)) {
          Dialog({ message: '手机号码有误，请重填' }).then(() => {
            this.$refs.phoneInput.focus()
          })
          this.click = false
          return
        }
        const { order_num: orderNum, product_sn: productSn } = await this.$apis.payment.pay({
          phone,
          code,
          product_sn: 'PRODUCT_SN_XIAOBAI_0',
          ...getSearch()
        })
        localStorage.setItem('buyPhone', phone)
        localStorage.setItem('is_pay', '1')
        // 成功回调
        typeof successBack === 'function' && successBack()
        this.click = false
        this.hrefToSuccess(productSn, orderNum)
      } catch (error) {
        this.click = false
        const { data } = error
        // 失败回调
        typeof errorBack === 'function' && errorBack(data)
        if (error.code === 30006) {
          this.$dialog({
            message: error.msg,
            confirmButtonText: '查看分班'
          }).then(res => {
            this.hrefToSuccess(error.data.product_sn, error.data.order_num)
          })
        }
      }
    },
    /**
     * @Date: 2021-01-08 10:54:08
     * @author: zw
     * @description: 跳转到引导页
     */
    hrefToSuccess (productSn, orderNum) {
      // 根据返回的商品编号判断跳转到对应的公众号
      const toOfficialAccount = {
        PRODUCT_SN_XIAOBAI_12: 'SuccessEnrollWM',
        PRODUCT_SN_XIAOBAI_7: 'SuccessEnrollWM',
        PRODUCT_SN_XIAOBAI_1: 'SuccessEnrollMH',
        PRODUCT_SN_XIAOBAI_0: 'SuccessEnrollMH'
      }
      localStorage.setItem('productSn', productSn)
      localStorage.setItem('orderNum', orderNum)
      this.$router.push({
        name: toOfficialAccount[productSn],
        query: {
          orderNum
        }
      }).catch(err => {
        console.log(err)
      })
    },
    /**
     * @Date: 2021-01-06 11:06:07
     * @author: zw
     * @description: 支付成功回调 提交按钮埋点
     */
    paySuccessBack () {
      this.$log.click({
        current_page: location.href,
        module_ori: '表单提交',
        module_part: this.SUBMIT_BTN
      })
    },
    /**
     * @Date: 2021-01-06 15:06:07
     * @author: zw
     * @description: 支付成功回调 表单错误模块埋点
     */
    payErrorBack (data) {
      const codeMap = {
        33002: this.ERROR_CODE,
        30001: this.STUDENT_OVER,
        30003: this.STUDENT_OVER,
        30004: this.ERROR_BUYED,
        30002: this.ERROR_BUYED
      }
      if (!codeMap[data.code]) return
      this.$log.click({
        current_page: location.href,
        module_ori: '表单错误模块',
        module_part: codeMap[data.code]
      })
    },
    /**
     * @Date: 2021-01-06 15:22:25
     * @author: zw
     * @description: 获取验证码成功回调 获取验证码埋点
     */
    getCodeSuccessBack () {
      this.$log.click({
        module_ori: '表单提交',
        module_part: this.GET_CODE
      })
    },
    /**
     * @Date: 2021-01-06 15:22:25
     * @author: zw
     * @description: 获取验证码成功回调
     */
    getCodeErrorBack () {
      this.$log.click({
        current_page: location.href,
        module_ori: '表单错误模块',
        module_part: this.ERROR_PHONE
      })
    }
  }
}
</script>
